/* Breadcrumbs (below multiple editor tabs) */

.monaco-workbench
	.part.editor
	> .content
	.editor-group-container
	> .title
	.breadcrumbs-below-tabs
	.breadcrumbs-control {
	flex: 1 100%;
	height: 22px;
	cursor: default;
}

.monaco-workbench
	.part.editor
	> .content
	.editor-group-container
	> .title
	.breadcrumbs-below-tabs
	.breadcrumbs-control
	.monaco-icon-label {
	height: 22px;
	line-height: 22px;
}

.monaco-workbench
	.part.editor
	> .content
	.editor-group-container
	> .title
	.breadcrumbs-below-tabs
	.breadcrumbs-control
	.monaco-icon-label::before {
	height: 22px; /* tweak the icon size of the editor labels when icons are enabled */
}

.monaco-workbench
	.part.editor
	> .content
	.editor-group-container
	> .title
	.breadcrumbs-below-tabs
	.breadcrumbs-control
	.outline-element-icon {
	padding-right: 3px;
	height: 22px; /* tweak the icon size of the editor labels when icons are enabled */
	line-height: 22px;
}

.monaco-workbench
	.part.editor
	> .content
	.editor-group-container
	> .title
	.breadcrumbs-below-tabs
	.breadcrumbs-control
	.monaco-breadcrumb-item {
	max-width: 80%;
}

.monaco-workbench
	.part.editor
	> .content
	.editor-group-container
	> .title
	.breadcrumbs-below-tabs
	.breadcrumbs-control
	.monaco-breadcrumb-item::before {
	width: 16px;
	height: 22px;
	display: flex;
	align-items: center;
	justify-content: center;
}

.monaco-workbench
	.part.editor
	> .content
	.editor-group-container
	> .title
	.breadcrumbs-below-tabs
	.breadcrumbs-control
	.monaco-breadcrumb-item:last-child {
	padding-right: 8px;
}

.monaco-workbench
	.part.editor
	> .content
	.editor-group-container
	> .title
	.breadcrumbs-below-tabs
	.breadcrumbs-control
	.monaco-breadcrumb-item:last-child
	.codicon:last-child {
	display: none; /* hides chevrons when last item */
}
